import React from 'react'
import { Container } from 'react-bootstrap'

export default ( { players } ) => {

    let oddPlayers = players.filter(player => player.id % 2 !== 0)
    let evenPlayers = players.filter(player => player.id % 2 === 0)

    return (
        <Container>
            <ul>
                {oddPlayers.map((player, i) => (
                    <li key={i}>
                        <p> { player } </p>
                    </li>
                ))}
            </ul>
                    <h1>Vs</h1>
            <ul>
                {evenPlayers.map((player, i) => (
                    <li key={i}>
                        <p> { player } </p>
                    </li>
                ))}
            </ul>
        </Container>
    )

}