const reducer = (state, action) => {

    const addPlayers = (state, { playerList } ) => {
    
        const newPlayerList = playerList.map((player, i) => {
            return {    
                id: i+1,
                name: player,
            }
        })

        return {             
            ...state,
            players: newPlayerList
        }
        
    }


    const shuffle = state => {

        // As players have their own player ids, it is possible
        // that a concat and then shuffle and then split might
        // be a better solution here, but for now, this is more elegant.

        const list = state.players;
      
        list.sort(() => 0.5 - Math.random());
    
        console.log(list);
    
        this.setState({
          ...state, list
        });
    }
        


    switch(action.type) {

        case "ADD_PLAYERS": 
            return shuffle(addPlayers(state, action));
        case 'DELETE_PLAYER':
            return state.filter(player => player.id !== action.id);
        default: return state

    }

}

export default reducer;